@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&family=Mountains+of+Christmas:wght@400;700&family=Pacifico&family=Special+Elite&display=swap);
.quote{
    background-color: #343a40;
    border:none;
}
.body p{
    color:whitesmoke;
    font-family:cursive;
}
.body footer{
    color:rgb(219, 199, 163);
